          <a class="tab-btn" href="javascript:;" :class="activeTab == '2' ? 'active' : ''" @click="tabClick('2')">法定职责</a>
          <a class="tab-btn" href="javascript:;" :class="activeTab == '3' ? 'active' : ''" @click="tabClick('3')">内设机构</a>
          <a class="tab-btn" href="javascript:;" :class="activeTab == '4' ? 'active' : ''" @click="tabClick('4')">所属机构</a>
          <!-- <CloseBtn routeName="Home" /> -->
        </div>
        <!-- 基本信息 -->
        <BasicInfo v-if="activeTab=='1'" />
        <!-- 法定职责 -->
        <StatutoryDuties v-else-if="activeTab=='2'" />
        <!-- 内设机构 -->
        <InnerMechanism v-else-if="activeTab=='3'" />
        <!-- 所属机构 -->
